.doc-cards {
  /* display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 20px;  
  flex-wrap: wrap; */

  display: flex;
  flex-direction: row;
  justify-content:center;
  column-gap: 2%; 
  flex-wrap: wrap;
  margin-left: 8%;
}

.doc-card {
  /* opacity: 0.8; */
  background-color: #c8ad7fb6;
  border-radius: 30px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 300px;
  margin: 20px 0;
}

.doc-card:hover {
  opacity: 1;
  /* box-shadow: 0 16px 32px 0 rgba(0, 0, 0, 0.2); */
  box-shadow: 0 0 10px black, 0 0 20px black, 0 0 40px black;
}

.doc-card-container {
  text-align: center;
  color: black;
  padding: 2px 16px;
}

.doc-card-top {
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  padding-top: 30px;
}

.doc-card h4 {
  display: block;
}

.doc-link {
  text-decoration: none;
}

.doc-link:active {
  transform: translateY(4px);
}
